@import url(https://fonts.googleapis.com/css?family=Pacifico|Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1f1f1f;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #38b3be;
}

h1 {
  font-size: 7em;
  margin-bottom: 25px;
  font-family: 'Pacifico', cursive;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#textField {
  width: 500px;
  height: 50px;
  font-size: 2em;
  text-align: center;
  background-color: #1f1f1f;
  border: 3px solid #5f5f5f;
  border-radius: 25px;
  outline: none;
  color: #38b3be;
  margin-bottom: 25px;
  font-family: 'Roboto', sans-serif;
  padding: 2px;
}

#button {
  width: 400px;
  height: 50px;
  font-size: 2em;
  background-color: #38b3be;
  border-radius: 25px;
  color: #1f1f1f;
  outline: none;
  border: none;
  font-family: 'Roboto', sans-serif;
}

#button:hover {
  background-color: #1f1f1f;
  border: 3px solid #38b3be;
  color: #38b3be;
}

